const host = "http://www.iyayatv.com";

const request = {
    search(str, callback = function () { }) {
        let settings = {
            url: `/api/vodsearch/-------------.html?wd=${str}&submit=`,
            dataType: "document",
            success(doc, status, xhr) {
                let li = doc.querySelectorAll(".vodlist.clearfix li");
                let json = {
                    count: li.length,//搜索结果个数
                    data: Array.prototype.map.call(li, e => {
                        let a = e.querySelector(".vodlist_thumb.lazyload");
                        let div = e.querySelector(".searchlist_titbox");
                        return {
                            title: a.title,//标题
                            href: a.href,//链接
                            bg: a.getAttribute("data-original"),//背景图
                            pic_text: a.querySelector(".pic_text.text_right").innerText,//提示
                            type: div.querySelector(".info_right").innerText,//影视类型
                            to_star: div.querySelector(".vodlist_sub").innerText,//主演
                            director: div.querySelectorAll(".vodlist_sub")[1].innerText,//导演
                            describe: div.querySelector(".vodlist_sub.hidden_xs").innerText//简介
                        }
                    })
                }
                callback(json);
            },
            error(xhr, status, error) {
                console.log(error);
            }
        }
        http.ajax(settings);
    },
    home(callback = function () { }) {
        let settings = {
            url: `/api`,
            dataType: "document",
            success(doc, status, xhr) {
                let hot_li = doc.querySelectorAll(".vodlist.vodlist_wi.list_v12.clearfix li");
                let json = {
                    count: hot_li.length,//搜索结果个数
                    data: Array.prototype.map.call(hot_li, (e, i) => {
                        let a = e.querySelector(".vodlist_thumb.lazyload");
                        let div = e.querySelector(".vodlist_titbox");
                        let bg = a.getAttribute("data-original");
                        bg = bg.includes("http://") ? bg : host + bg;
                        return {
                            id: i,
                            title: a.title,//标题
                            href: a.href,//链接
                            bg,//背景图
                            pic_text: a.querySelector(".pic_text.text_right").innerText,//提示
                            year: utils.safeParse(a.querySelector(".voddate.voddate_year"), "innerText"),//年份
                            type: utils.safeParse(a.querySelector(".voddate.voddate_type"), "innerText"),//影视类型
                            to_star: div.querySelector(".vodlist_sub").innerText//主演
                        }
                    })
                }
                callback(json);
            },
            error(xhr, status, error) {
                console.log(error);
            }
        }
        http.ajax(settings);
    }
}

export default request;

const utils = {
    //安全解析数据，防止有些DOM节点是不存在的造成程序出错
    safeParse(node, param) {
        return node ? eval("node." + param) : "";
    }
}

const http = {
    ajax: (settings = {}) => {
        // 初始化请求参数
        let _s = Object.assign({
            url: '', // string
            type: 'GET', // string 'GET' 'POST' 'DELETE'
            dataType: 'json', // string 期望的返回数据类型:'json' 'text' 'document' ...
            async: true, //  boolean true:异步请求 false:同步请求 required
            data: null, // any 请求参数,data需要和请求头Content-Type对应
            headers: {}, // object 请求头
            timeout: 5000, // string 超时时间:0表示不设置超时
            beforeSend: (xhr) => {
            },
            success: (result, status, xhr) => {
            },
            error: (xhr, status, error) => {
            },
            complete: (xhr, status) => {
            }
        }, settings);
        // 参数验证
        if (!_s.url || !_s.type || !_s.dataType || !_s.async) {
            alert('参数有误');
            return;
        }
        // 创建XMLHttpRequest请求对象
        let xhr = new XMLHttpRequest();
        // 请求开始回调函数
        xhr.addEventListener('loadstart', e => {
            _s.beforeSend(xhr);
        });
        // 请求成功回调函数
        xhr.addEventListener('load', e => {
            const status = xhr.status;
            if ((status >= 200 && status < 300) || status === 304) {
                let result;
                if (xhr.responseType === 'text') {
                    result = xhr.responseText;
                } else if (xhr.responseType === 'document') {
                    result = xhr.responseXML;
                } else {
                    result = xhr.response;
                }
                // 注意:状态码200表示请求发送/接受成功,不表示业务处理成功
                _s.success(result, status, xhr);
            } else {
                _s.error(xhr, status, e);
            }
        });
        // 请求结束
        xhr.addEventListener('loadend', e => {
            _s.complete(xhr, xhr.status);
        });
        // 请求出错
        xhr.addEventListener('error', e => {
            _s.error(xhr, xhr.status, e);
        });
        // 请求超时
        xhr.addEventListener('timeout', e => {
            _s.error(xhr, 408, e);
        });
        let sType = _s.type.toUpperCase();
        // 如果是"简单"请求,记录下来
        let useUrlParam = sType === 'GET' || sType === 'DELETE';
        // 初始化请求
        xhr.open(_s.type, _s.url, _s.async);
        // 设置期望的返回数据类型
        xhr.responseType = _s.dataType;
        // 设置请求头
        for (const key of Object.keys(_s.headers)) {
            xhr.setRequestHeader(key, _s.headers[key]);
        }
        // 设置超时时间
        if (_s.async && _s.timeout) {
            xhr.timeout = _s.timeout;
        }
        // 发送请求.如果是简单请求,请求参数应为null.否则,请求参数类型需要和请求头Content-Type对应
        xhr.send(useUrlParam ? null : http.getQueryData(_s.data));
    },
    // 获取ajax请求参数
    getQueryData: (data) => {
        if (!data) {
            return null;
        }
        if (typeof data === 'string') {
            return data;
        }
        if (data instanceof FormData) {
            return data;
        }
        return http.getQueryString(data);
    },
    // 把对象转为查询字符串
    getQueryString: (data) => {
        let paramsArr = [];
        if (data instanceof Object) {
            Object.keys(data).forEach(key => {
                let val = data[key];
                // todo 参数Date类型需要根据后台api酌情处理
                if (val instanceof Date) {
                    // val = dateFormat(val, 'yyyy-MM-dd hh:mm:ss');
                }
                paramsArr.push(encodeURIComponent(key) + '=' + encodeURIComponent(val));
            });
        }
        return paramsArr.join('&');
    }
}